  font-smoothing: antialiased;
}

.green-light:before {
  content: $circle;
  color: $icon-color-green;
}
.yellow-light:before {
  content: $circle;
  color: $icon-color-warning;
}
.red-light:before {
  content: $circle;
  color: $icon-color-danger;
}
.gray-light:before {
  content: $circle;
  color: $icon-color-light;
  font-size: 14px;
  display: block;
  position: absolute;
  white-space: normal;
  text-indent: 0;
  font-family: 'okticon';
  font-weight: 300 !important;
  font-style: normal !important;
  speak: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-smoothing: antialiased;
}
.legend-light:before {
  color: #f9f9f9;
  content: $circle;
}
.legend-light:after {
  color: $icon-color-dark;
  content: $circle-border;
}

